<template>
	<div class="open-pay-password">
		<div class="open-pay-password-header">
			<mt-header title="">
			  <router-link to="/" slot="left">
			    <mt-button icon="back"></mt-button>
			  </router-link>
			</mt-header>
		</div>
		<div class="open-pay-password-keyboard">
			<div class="open-pay-password-keyboard-title">
				<span>6</span>
			</div>
			<!--  -->
			<van-password-input
			  :value="value"
			  :mask="true"
			  info=""
			  @focus="showKeyboard = true"
			/>
			<div class="open-pay-password-keyboard-title">
				<span></span>
			</div>
			<van-password-input
			  :value="value"
			  :mask="true"
			  @focus="showKeyboard = true"
			/>
			<!--  -->
			<van-number-keyboard
			  :show="showKeyboard"
			  @input="onInput"
			  @delete="onDelete"
			  @blur="showKeyboard = false"
			/>
		</div>
		<div class="rest-pass-word-btn">
			<mt-button type="primary" size="large"></mt-button>
		</div>
		<div class="open-pay-password-keyboard-text">
			<p></p>
			<p></p>
		</div>
	</div>
</template>

<script>

export default {
  data() {
    return {
      value: '',
      showKeyboard: false
    };
  },

  methods: {
    onInput(key) {
      this.value = (this.value + key).slice(0, 6);
    },
    onDelete() {
      this.value = this.value.slice(0, this.value.length - 1);
    }
  }
}
</script>

<style lang="scss">
	@import '../../../assets/scss/global'
</style>


