 import {Toast} from '../../../node_modules/mint-ui'
var pub = {
	//
sendEmail(email){
	    var regEmail=/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/
	    if(!regEmail.test(email)){
	       Toast({
				message: ''
			})
	    }
	},
	//  
passWord(pass){
		var regpass = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/
		if(!regpass.test(pass)){
			Toast({
				message: '8-16+'
			})
		}
	}
}
export default pub




